.p-radiobutton {
    display:inline-block;
    cursor: pointer;
    vertical-align: middle;
    margin-right: .25em;
    -ms-user-select: none;
        user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}

.p-radiobutton .p-radiobutton-box {
    width: 1.125em;
    height: 1.125em;
    line-height: 1.125em;
    border-radius: 100%;
    text-align: center;
    position: relative;
}

.p-radiobutton .p-radiobutton-icon {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1em;
    height: 1em;
    margin-top: -.5em;
    margin-left: -.5em;
}
 
.p-radiobutton + label {
    vertical-align: middle;
}


