.p-overlaypanel {
    padding: 0;
    margin: 0;
    position: absolute;
    display: none;
}

.p-overlaypanel-content {
    padding: 0.5em 1em;
}

.p-overlaypanel-close {
    position: absolute;
    top: -1em;
    right: -1em;
    width: 2em;
    height: 2em;
    line-height: 2em;
    text-align: center;
    border-radius: 100%;
}

.p-overlaypanel-close-icon {
    line-height: inherit;
}


