.p-slider {
	position: relative;
	text-align: left;
}
.p-slider .p-slider-handle {
	position: absolute;
	width: 1.2em;
	height: 1.2em;
	cursor: default;
	-ms-touch-action: none;
	touch-action: none;
    z-index: 2;
}
.p-slider .p-slider-handle.p-slider-handle-active {
    z-index: 2;
}
.p-slider .p-slider-range {
	position: absolute;
	font-size: .7em;
	display: block;
	border: 0;
	background-position: 0 0;
}

.p-slider-horizontal {
	height: .8em;
}
.p-slider-horizontal .p-slider-handle {
	top: -.3em;
	margin-left: -.6em;
}
.p-slider-horizontal .p-slider-range {
	top: 0;
	height: 100%;
}
.p-slider-horizontal .p-slider-range-min {
	left: 0;
}
.p-slider-horizontal .p-slider-range-max {
	right: 0;
}

.p-slider-vertical {
	width: .8em;
	height: 100px;
}
.p-slider-vertical .p-slider-handle {
	left: -.3em;
	margin-left: 0;
	margin-bottom: -.6em;
}
.p-slider-vertical .p-slider-range {
	left: 0;
	width: 100%;
}
.p-slider-vertical .p-slider-range-min {
	bottom: 0;
}
.p-slider-vertical .p-slider-range-max {
	top: 0;
}

.p-slider-animate .p-slider-handle {
    -webkit-transition: left .3s;
    -o-transition: left .3s;
    transition: left .3s;
}


